@import '../css/colors.scss';
@import '../css/fonts.scss';

.profile-view {
    color: $text-primary  !important;
    box-sizing: border-box;

    position: absolute;
    width: 180px;
    // height: 240px;
    height: 155px;
    top: 50px;

    /* Blue/Blue 3 */

    background: #132F4C;
    /* Border/Card Border */

    border: 1px solid #1E4976;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    align-items: center;
    text-align: center;

    .address {
        position: relative;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        /* identical to box height */
        top: 10px;
    }

    .line1 {
        position: relative;
        width: 155px;
        height: 0px;
        left: 13px;
        top: 20px;

        opacity: 0.99;
        border-top: 1px solid #3399FF;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    .your-balance {
        position: relative;
        font-size: 15px;
        line-height: 20px;
        top: 30px;
        text-align: center;

        .icon-container {
            height: 40px;

            .total {
                font-family: roboto;
                font-size: 20px;
                line-height: 26px;
                text-align: center;
                width: 36px;
            }

            .coin-img {
                margin: 0 5px;
                width: 24px;
                height: 24px;
            }

            .spaces {
                font-family: roboto;
                font-size: 12px;
                line-height: 16px;
                color: $text-yellow;
                text-transform: uppercase;
                width: 36px;
            }
        }

        .btn-claim {

            width: 80px;
            height: 25px;
            left: 50px;
            top: 113px;
        }

        .line2 {
            position: relative;
            width: 120px;
            height: 0px;
            left: 30px;
            top: 10px;

            opacity: 0.99;
            border-top: 1px solid #3399FF;
            box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        }

    }


}