@import './colors.scss';
@import './fonts.scss';

.spaces-container-parent {
    width: 200px;
    display: flex;
    justify-content: right;

    .spaces-container {
        width: 100px;
        padding: 8px;

        background: $blue-three;
        border: 1px solid $card-border;
        border-radius: 8px;

        cursor: pointer;

        .plus-img {}

        .spaces-content {
            font-family: roboto;
            font-size: 12px;
            line-height: 16px;
            color: $text-primary;
            margin: unset;
        }
    }
}

.DepositDialog {

    color: white;
    background: #132F4C;
    /* Border/Card Border */
    border: 1px solid #1E4976;
    box-sizing: border-box;

    .dialogContent {
        .description {
            color: white;
        }

        .depositText {
            color: white;
        }
    }

    .actionButton {
        // background-color: white;
        // width:40px;
        font-weight: 900;
    }
}