@import './colors.scss';
@import './fonts.scss';

.claimButton {
    background-color: $primary;

    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    box-shadow: $button-box-shadow;
    padding: 16px 20px;
    border-radius: 8px;
    border: none;
    width: 80px;
    height: 25px;

    font-family: 'roboto';
    color: $text-primary;
    text-align: center;
    line-height: 18px;
    font-weight: 500;
    font-size: 14px;

    cursor: pointer;
}